.header {
  padding: 15px 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
  }
}
.nav {
  display: flex;
  padding-right: 18px;
  &__list {
    margin-right: 32px;
    display: flex;
    gap: 51px;
    @include small-tablet {
      flex-direction: column;
    }
  }

  &__link {
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    color: var(--white);
    transition: color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
    @include small-tablet {
      font-size: 18px;
    }
  }
  @include small-tablet {
    position: fixed;
    top: 0;
    left: 0;
    width: 80%;
    height: 100vh;
    flex-direction: column;
    gap: 50px;
    background-color: #001414;
    padding: 50px 15px;
    overflow: auto;
    transform: translateX(-100%);
    z-index: 100;
    transition: transform 0.4s ease-in-out;
  }
}
.burger {
  @include burger;
  display: none;
  color: var(--white);
  height: 15px;
  @include small-tablet {
    display: block;
  }
}
.menu--active {
  transform: translateX(0);
}
