.press {
  &__top {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    gap: 50px;
    margin-bottom: 33px;
    @include mobile {
      justify-content: center;
    }
  }

  &__title {
    margin: 0;
    font-family: "Libre Baskerville";
    font-weight: 400;
    font-size: 27px;
    line-height: 38px;
    text-align: center;
    letter-spacing: 1.2px;
    color: var(--dark-green);
    @include mobile {
      font-size: 21px;
    }
  }

  &__link {
    border: 1px solid rgba(53, 83, 98, 0.2);
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.02px;
    color: var(--dark-green);
    transition: color 0.4s ease-in-out, border-color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
      border-color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;
    @include small-tablet {
      grid-template-columns: repeat(8, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(4, 1fr);
    }
  }

  &__item {
    grid-column: 4 span;
  }
}
.press-card {
  position: relative;
  border-top: 1px solid rgba(53, 83, 98, 0.3);
  padding-top: 16px;
  min-height: 145px;
  transition: border-color 0.4s ease-in-out;
  @include hover {
    border-color: var(--red);
    .press-card__link {
      color: var(--blue);
    }
  }
  &__top {
    display: flex;
    align-items: center;
    gap: 16px;
    margin-bottom: 11px;
  }

  &__author {
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
    padding: 20px 0;
    padding-left: 35px;
    background-size: 24px 24px;
    background-position: top 15px left;
    background-repeat: no-repeat;
    &--ted {
      background-image: url(../img/approach-icon-1.jpg);
    }
  }

  &__data {
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
  }

  &__link {
    display: block;
    margin-top: -20px;
    font-weight: 400;
    font-size: 17px;
    line-height: 25px;
    color: var(--dark-green);
    transition: color 0.4s ease-in-out;
    &::after {
      content: "";
      position: absolute;
      inset: 0;
    }
    &:focus {
      color: var(--red);
    }
  }
}
