.info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #001414;
  height: 680px;
  @include small-tablet {
    padding: 50px 15px;
    height: auto;
  }
  &__decor {
    max-width: 287px;
    height: 100%;
    @include tablet {
      max-width: 140px;
    }
    @include small-tablet {
      display: none;
    }
  }

  &__content {
    display: flex;
    flex-direction: column;
    border-left: #001414;
    border-right: #001414;
    padding: 25px 82px;
    @include small-tablet {
      padding: 0;
    }
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: var(--white);
    margin-bottom: 21px;
    @include mobile {
      font-size: 18px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--white);
    @include mobile {
      font-size: 13px;
    }
  }
}
