.steps {
  padding: 120px 0;
  @include small-tablet {
    padding: 50px 0;
  }
  &__title {
    margin: 0 auto;
    max-width: 717px;
    font-family: "Libre Baskerville";
    font-weight: 400;
    font-size: 30px;
    line-height: 38px;
    text-align: center;
    letter-spacing: 1.2px;
    color: var(--dark-green);
    margin-bottom: 57px;
    @include mobile {
      font-size: 21px;
    }
  }

  &__list {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    max-width: 780px;
    counter-reset: steps;
    @include small-tablet {
      flex-direction: column;
      align-items: center;
      row-gap: 90px;
    }
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
    max-width: 130px;
    text-align: center;
    &::before {
      counter-increment: steps;
      content: counter(steps);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 28px;
      border-radius: 100%;
      width: 48px;
      height: 48px;
      font-weight: 400;
      font-size: 16px;
      line-height: 30px;
      text-align: center;
      color: var(--white);
      background-color: var(--dark-green);
    }
    &:not(:last-child) {
      &::after {
        content: "";
        position: absolute;
        right: -181px;
        top: 50px;
        width: 175px;
        height: 1px;
        background: rgba(53, 83, 98, 0.2);
        @include small-tablet {
          display: none;
        }
      }
    }
    @include small-tablet {
      max-width: none;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    text-align: center;
    color: rgba(53, 83, 98, 0.8);
  }
}
