.approach {
  padding-bottom: 25px;
  &__wrapper {
    padding: 80px 0;
    background-color: #f7f8f8;
    @include small-tablet {
      padding: 50px 0;
    }
  }
  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include small-tablet {
      flex-direction: column;
      row-gap: 50px;
    }
  }

  &__left {
    max-width: 450px;
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: var(--dark-green);
    margin-bottom: 13px;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: rgba(53, 83, 98, 0.8);
    margin-bottom: 37px;
  }

  &__btn {
    border: 1px solid rgba(53, 83, 98, 0.2);
    font-weight: 400;
    font-size: 13px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.02px;
    color: var(--dark-green);
    transition: color 0.4s ease-in-out, border-color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
      border-color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
  }

  &__img {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 8px;
    @include small-tablet {
      img {
        width: 100%;
      }
    }
  }

  &__bottom {
    margin-top: 25px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px;
    @include tablet {
      grid-template-columns: repeat(6, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  &__item {
    grid-column: 2 span;
    @include mobile {
      grid-column: 1 span;
    }
  }
  &__link {
    display: inline-block;
    width: 100%;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    border-radius: 0px;
    padding-top: 16px;
    padding-left: 35px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: rgba(53, 83, 98, 0.8);
    background-size: 24px 24px;
    background-position: top 15px left;
    background-repeat: no-repeat;
    transition: color 0.4s ease-in-out, border-color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
      border-color: var(--red);
    }
    &:focus {
      color: var(--red);
    }
  }

  &__link--ted {
    background-image: url(../img/approach-icon-1.jpg);
  }

  &__link--tny {
    background-image: url(../img/approach-icon-2.jpg);
  }

  &__link--scoop {
    background-image: url(../img/approach-icon-3.jpg);
  }

  &__link--press {
    background-image: url(../img/approach-icon-4.jpg);
  }

  &__link--time {
    background-image: url(../img/approach-icon-5.jpg);
  }

  &__link--freethink {
    background-image: url(../img/approach-icon-6.jpg);
  }
}
