.information {
  padding-bottom: 141px;
  @include small-tablet {
    padding-bottom: 50px;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 40px;
    @include tablet {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
  }

  &__title {
    margin: 0;
    max-width: 425px;
    font-weight: 400;
    font-size: 31px;
    line-height: 38px;
    letter-spacing: 1.2px;
    color: var(--dark-green);
    @include tablet {
      text-align: center;
    }
    @include mobile {
      font-size: 21px;
    }
  }

  &__list {
    max-width: 560px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 36px 48px;
    @include tablet {
      max-width: none;
      gap: 40px;
    }
    @include mobile {
      flex-direction: column;
    }
  }

  &__item {
    flex: 0 1 248px;
    @include tablet {
      flex: 0 1 40%;
    }
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--dark-green);
    margin-bottom: 5px;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: rgba(53, 83, 98, 0.8);
  }
}
