.hero {
  background-image: url(../img/hero-bg.jpg);
  @include bg-cover;
  padding: 180px 0 40px 0;
  @include small-tablet {
    padding-top: 90px;
  }
  &__container {
    display: flex;
    flex-direction: column;
  }

  &__content {
    max-width: 542px;
    margin-bottom: auto;
  }

  &__title {
    margin: 0;
    font-family: "Libre Baskerville";
    font-weight: 400;
    font-size: 38px;
    line-height: 44px;
    letter-spacing: 1.2px;
    color: var(--white);
    margin-bottom: 29px;
    @include mobile {
      font-size: 21px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: var(--white);
    margin-bottom: 29px;
    @include mobile {
      font-size: 14px;
    }
  }

  &__btn {
    display: inline-block;
    border: 1px solid rgba(255, 255, 255, 0.3);
    color: var(--white);
    transition: color 0.4s ease-in-out, border-color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
      border-color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
  }

  &__bottom {
    margin-top: 122px;
    @include small-tablet {
      margin-top: 60px;
    }
  }

  &__subtitle {
    margin: 0;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--white);
    margin-bottom: 33px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;
    @include tablet {
      grid-template-columns: repeat(6, 1fr);
    }
    @include mobile {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  &__item {
    grid-column: 2 span;
    @include mobile {
      grid-column: 1 span;
    }
  }

  &__link {
    padding-top: 17px;
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    display: inline-block;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--white);
    transition: color 0.4s ease-in-out, border-color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
      border-color: var(--red);
    }
    &:focus {
      color: var(--red);
    }
  }
}
