.footer {
  &__top {
    padding: 112px 0 65px 0;
    background-color: #001414;
    @include small-tablet {
      padding: 50px 0;
    }
  }
  &__row {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 32px;
    @include small-tablet {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  &__col {
    grid-column: 2 span;
    &:last-child {
      grid-column: 11 / 2 span;
      @include small-tablet {
        grid-column: 2 span;
      }
    }
  }
  &__title {
    position: relative;
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #7d9798;
    margin-bottom: 23px;
    padding-bottom: 24px;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background-color: #25b895;
    }
  }
  &__link {
    font-weight: 400;
    font-size: 15px;
    line-height: 30px;
    color: var(--white);
    transition: color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
  }
  &__bottom {
    padding: 45px 0 63px 0;
    background-color: #000f0f;
    @include small-tablet {
      padding: 40px 0;
    }
  }

  &__bottom-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    gap: 50px;
  }

  &__sublist {
    display: flex;
    gap: 8px;
    margin-bottom: 8px;
  }

  &__sublist-link {
    font-weight: 400;
    font-size: 10px;
    line-height: 10px;
    color: var(--white);
    transition: color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
  }

  &__info {
    display: flex;
  }

  &__descr {
    font-weight: 400;
    font-size: 9px;
    line-height: 10px;
    color: var(--white);
  }
}
