.work {
  background-color: #001f1f;
  padding: 145px 0 135px 0;
  @include small-tablet {
    padding: 50px 0;
  }
  &__container {
    display: flex;
    justify-content: space-between;
    gap: 50px;
    @include small-tablet {
      flex-direction: column;
      align-items: center;
    }
  }

  &__content {
    max-width: 305px;
    @include small-tablet {
      text-align: center;
    }
  }

  &__title {
    margin: 0;
    font-family: "Libre Baskerville";
    font-weight: 400;
    font-size: 36px;
    line-height: 52px;
    letter-spacing: 1.2px;
    color: var(--white);
    margin-bottom: 43px;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: var(--white);
    margin-bottom: 37px;
  }

  &__btn {
    font-weight: 400;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.02px;
    color: var(--white);
    background-color: #00665f;
    border: 1px solid transparent;
    transition: color 0.4s ease-in-out, border-color 0.4s ease-in-out;
    @include hover {
      border-color: var(--blue);
      color: var(--blue);
    }
  }

  &__img {
    padding-right: 95px;
    @include tablet {
      padding-right: 0;
    }
  }
}
