.partners {
  padding: 17px 0;
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 7px;
    @include small-tablet {
      flex-direction: column;
      row-gap: 50px;
    }
  }

  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
  }

  &__list {
    display: flex;
    gap: 13px;
    @include mobile {
      flex-direction: column;
    }
  }
  &__item {
    @include small-tablet {
      &:nth-child(n + 4) {
        display: none;
      }
    }
  }
  &__link {
    display: block;
    max-width: 160px;
  }
  &__all {
    flex-shrink: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
    transition: color 0.4s ease-in-out;
    @include hover {
      color: var(--blue);
    }
    &:focus {
      color: var(--red);
    }
  }
}
